<template>
  <div>
    <div class="rekapitulasi-rating">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <base-header type="primary">
        <div class="row align-items-center py-2">
          <div class="col-12 text-right">
            <el-select
              v-if="isAdmin"
              v-model="selects.unitID"
              size="mini"
              placeholder="Pilih Unit Kerja"
              @change="getServerRating(selects.unitID)"
            >
              <el-option
                v-for="option in cabang"
                :key="option.label"
                :label="option.label"
                :value="option.value"
              ></el-option>
            </el-select>
            <base-button
              class="ml-2"
              size="sm"
              type="neutral"
              @click="
                () => {
                  this.$router.push('/gp/rating/ubah');
                }
              "
              >Ubah</base-button
            >
            <base-button
              size="sm"
              type="danger"
              @click="
                () => {
                  this.$router.push('/gp/rating/daftar');
                }
              "
              >Kembali</base-button
            >
          </div>
        </div>
      </base-header>
      <div v-if="renders.loading" class="container-fluid mt-4">
        <loading
          :active.sync="renders.loading"
          :can-cancel="true"
          :on-cancel="onCancel"
          :is-full-page="true"
          loader="dots"
        ></loading>
      </div>
      <div v-if="!renders.loading" class="container-fluid mt-4">
        <div class="row card-wrapper">
          <div class="col-lg-6">
            <card>
              <h5 slot="header" class="h3 mb-0">Nama Perusahaan</h5>
              <h1 class="card-text mb-4">{{ rekapitulasi.companyName }}</h1>
            </card>
          </div>
          <div class="col-lg-3">
            <card>
              <h5 slot="header" class="h3 mb-0">Tanggal</h5>
              <h1 class="card-text mb-4">
                {{ unixTS2DMY(rekapitulasi.timestamp) }}
              </h1>
            </card>
          </div>
          <div class="col-lg-3">
            <card class="overflow-hidden">
              <h5 slot="header" class="h3 mb-0">Periode</h5>
              <h1 class="card-text mb-4" style="height: 39px">
                {{ rekapitulasi.periode }}
              </h1>
            </card>
          </div>
        </div>
        <div class="row card-wrapper">
          <div class="col-lg-6">
            <card>
              <h5 slot="header" class="h3 mb-0">Cabang/Unit Bisnis</h5>
              <h1 class="card-text mb-4">{{ rekapitulasi.unitName }}</h1>
            </card>
          </div>
          <div class="col-lg-6">
            <div class="row card-wrapper">
              <div class="col-lg-6">
                <card>
                  <h5 slot="header" class="h3 mb-0">Hasil Rating</h5>
                  <h1 class="card-text mb-4">
                    {{ rekapitulasi.rating.toFixed(2) + "%" }}
                  </h1>
                </card>
              </div>
              <div class="col-lg-6">
                <card>
                  <h5 slot="header" class="h3 mb-0">Level</h5>
                  <h1 class="card-text mb-4">
                    {{ levelHandler(rekapitulasi.rating) }}
                  </h1>
                </card>
              </div>
            </div>
          </div>
          <div class="col-12">
            <div class="card">
              <div
                class="border-0 card-header d-flex align-items-center justify-content-between"
              >
                <h3 class="mb-0">Aspek Manajemen</h3>
              </div>

              <el-table
                class="table-responsive table-flush"
                header-row-class-name="thead-light"
                :data="rekapitulasi.management"
                @row-click="openDetail"
              >
                <el-table-column
                  label="Kode Aspek"
                  min-width="160px"
                  prop="code"
                  align="center"
                  sortable
                ></el-table-column>
                <el-table-column
                  label="Nama Aspek"
                  prop="name"
                  min-width="480px"
                  sortable
                ></el-table-column>
                <el-table-column
                  label="Skor Maksimal"
                  min-width="160px"
                  align="right"
                  sortable
                >
                  <template v-slot="{ row }">
                    <div class="cell">{{ row.max + "%" }}</div>
                  </template>
                </el-table-column>
                <el-table-column
                  label="Hasil Bobot"
                  min-width="160px"
                  align="right"
                  sortable
                >
                  <template v-slot="{ row }">
                    <div class="cell">{{ row.value.toFixed(2) + "%" }}</div>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="col-12">
            <div class="card">
              <div
                class="border-0 card-header d-flex align-items-center justify-content-between"
              >
                <h3 class="mb-0">Aspek Teknis</h3>
              </div>

              <el-table
                class="table-responsive table-flush"
                header-row-class-name="thead-light"
                :data="rekapitulasi.technical"
                @row-click="openDetail"
              >
                <el-table-column
                  label="Kode Aspek"
                  min-width="160px"
                  prop="code"
                  align="center"
                  sortable
                ></el-table-column>
                <el-table-column
                  label="Nama Aspek"
                  prop="name"
                  min-width="480px"
                  sortable
                ></el-table-column>
                <el-table-column
                  label="Skor Maksimal"
                  min-width="160px"
                  align="right"
                  sortable
                >
                  <template v-slot="{ row }">
                    <div class="cell">{{ row.max + "%" }}</div>
                  </template>
                </el-table-column>
                <el-table-column
                  label="Hasil Bobot"
                  min-width="160px"
                  align="right"
                  sortable
                >
                  <template v-slot="{ row }">
                    <div class="cell">{{ row.value.toFixed(2) + "%" }}</div>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Table, TableColumn, Select, Option } from "element-ui";
import moment from "moment";
import cabang from "../cabang";
import rekapitulasi from "./rekapitulasi";
import rating from "./rating";
import kp from "./kp";
import pg from "./pg";
import pl from "./pl";
import mk from "./mk";
import ku from "./ku";
import kl from "./kl";
import at from "./at";
import dp from "./dp";
import dk from "./dk";
import em from "./em";
import ai from "./ai";
import hs from "./hs";
import ml from "./ml";
import lb from "./lb";
import sb from "./sb";
import Loading from "vue-loading-overlay";
import "vue-loading-overlay/dist/vue-loading.css";

const axios = require("axios");

export default {
  components: {
    Loading,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  data() {
    return {
      renders: {
        loading: true,
      },
      api: this.$store.getters.getAPI,
      cabang,
      table: {
        kp: kp,
        pg: pg,
        pl: pl,
        mk: mk,
        ku: ku,
        kl: kl,
        at: at,
        dp: dp,
        dk: dk,
        em: em,
        ai: ai,
        hs: hs,
        ml: ml,
        lb: lb,
        sb: sb,
      },
      rating,
      rekapitulasi,
      selects: {
        unitID: null,
      },
      count: {
        kp: 23,
        pg: 16,
        pl: 14,
        mk: 12,
        ku: 26,
        kl: 11,
        at: 6,
        dp: 17,
        dk: 4,
        em: 17,
        ai: 12,
        hs: 8,
        ml: 17,
        lb: 32,
        sb: 6,
      },
      max: {
        kp: 15,
        pg: 10,
        pl: 1.5,
        mk: 1.5,
        ku: 6,
        kl: 6,
        at: 6,
        dp: 6,
        dk: 5,
        em: 25,
        ai: 7.5,
        hs: 1.5,
        ml: 1.5,
        lb: 6,
        sb: 1.5,
      },
      portion: {
        kp: [0.2, 0.2, 0.1, 0.1, 0.2, 0.2],
        pg: [0.3, 0.3, 0.4],
        pl: [0.15, 0.15, 0.125, 0.125, 0.2, 0.125, 0.125],
        mk: [0.3, 0.4, 0.3],
        ku: [0.1, 0.1, 0.05, 0.05, 0.2, 0.25, 0.05, 0.2],
        kl: [0.3, 0.3, 0.3, 0.1],
        at: [0.15, 0.2, 0.15, 0.2, 0.15, 0.15],
        dp: [0.1, 0.1, 0.1, 0.05, 0.05, 0.1, 0.1, 0.4],
        dk: [0.65, 0.35],
        em: [0.2, 0.6, 0.2],
        ai: [0.1, 0.1, 0.3, 0.5],
        hs: [0.15, 0.3, 0.2, 0.2, 0.15],
        ml: [0.2, 0.1, 0.1, 0.2, 0.4],
        lb: [0.2, 0.075, 0.1, 0.075, 0.1, 0.075, 0.1, 0.075, 0.2],
        sb: [0.4, 0.3, 0.3],
      },
      start: {
        kp: [0, 5, 8, 12, 15, 20],
        pg: [0, 5, 10],
        pl: [0, 1, 2, 5, 6, 7, 9],
        mk: [0, 3, 8],
        ku: [0, 4, 7, 9, 12, 16, 19, 20],
        kl: [0, 2, 5, 7],
        at: [0, 1, 2, 3, 4, 5],
        dp: [0, 1, 2, 3, 4, 5, 6, 7],
        dk: [0, 2],
        em: [0, 6, 14],
        ai: [0, 3, 5, 10],
        hs: [0, 1, 3, 5, 7],
        ml: [0, 3, 4, 5, 6],
        lb: [0, 11, 13, 16, 20, 21, 23, 25, 27],
        sb: [0, 2, 4],
      },
      span: {
        kp: [5, 3, 4, 3, 5, 3],
        pg: [5, 5, 6],
        pl: [1, 1, 3, 1, 1, 2, 5],
        mk: [3, 5, 4],
        ku: [4, 3, 2, 3, 4, 3, 1, 6],
        kl: [2, 3, 2, 4],
        at: [1, 1, 1, 1, 1, 1],
        dp: [1, 1, 1, 1, 1, 1, 1, 10],
        dk: [2, 2],
        em: [6, 8, 3],
        ai: [3, 2, 5, 2],
        hs: [1, 2, 2, 2, 1],
        ml: [3, 1, 1, 1, 11],
        lb: [11, 2, 3, 4, 1, 2, 2, 2, 5],
        sb: [2, 2, 2],
      },
    };
  },
  computed: {
    isAdmin() {
      return this.$store.getters.isAdmin;
    },
  },
  methods: {
    unitID(name) {
      for (let index = 0; index < this.cabang.length; index++) {
        if (this.cabang[index].label === name) {
          return this.cabang[index].value;
        }
      }
    },
    openDetail(row, col, event) {
      switch (row.code) {
        case "KP":
          this.$router.push("kp");
          break;
        case "PG":
          this.$router.push("pg");
          break;
        case "PL":
          this.$router.push("pl");
          break;
        case "MK":
          this.$router.push("mk");
          break;
        case "KU":
          this.$router.push("ku");
          break;
        case "KL":
          this.$router.push("kl");
          break;
        case "AT":
          this.$router.push("at");
          break;
        case "DP":
          this.$router.push("dp");
          break;
        case "DK":
          this.$router.push("dk");
          break;
        case "EM":
          this.$router.push("em");
          break;
        case "AI":
          this.$router.push("ai");
          break;
        case "HS":
          this.$router.push("hs");
          break;
        case "ML":
          this.$router.push("ml");
          break;
        case "LB":
          this.$router.push("lb");
          break;
        case "SB":
          this.$router.push("sb");
          break;

        default:
          break;
      }
    },
    editRating() {
      this.$router.push("ubah").catch(() => {});
    },
    getStoreRating() {
      this.passData(this.$store.getters.getGPData);
      // this.replaceValue(this.rating);
    },
    getServerRating(unit) {
      // console.log("axios");
      axios
        .get(this.api.url + this.api.gp.getLastRating + unit)
        .then((response) => {
          // console.log(response.data);
          this.passDATA2Store(response.data);
          this.getStoreRating();
          this.renders.loading = false;
        })
        .catch((response) => {
          console.log(
            "ERROR: Failed to get data from server. ID:",
            unit,
            ".",
            response
          );
        });
    },
    passData(data) {
      this.rating.kp = data.kp;
      this.rating.pg = data.pg;
      this.rating.pl = data.pl;
      this.rating.mk = data.mk;
      this.rating.ku = data.ku;
      this.rating.kl = data.kl;
      this.rating.at = data.at;
      this.rating.dp = data.dp;
      this.rating.dk = data.dk;
      this.rating.em = data.em;
      this.rating.ai = data.ai;
      this.rating.hs = data.hs;
      this.rating.ml = data.ml;
      this.rating.lb = data.lb;
      this.rating.sb = data.sb;
      this.selects.unitID = this.unitID(data.unitName);
      this.rekapitulasi.timestamp = data.timestamp;
      this.rekapitulasi.periode = data.periode;
      this.rekapitulasi.companyName = "Pelindo 2";
      this.rekapitulasi.unitName = data.unitName;
      this.rekapitulasi.rating = this.ratingHandler(this.rating);
      this.rekapitulasi.management[0].value =
        (this.rekapitulasi.management[0].max / 100) *
        this.aspectHandler("kp", this.rating.kp);
      this.rekapitulasi.management[1].value =
        (this.rekapitulasi.management[1].max / 100) *
        this.aspectHandler("pg", this.rating.pg);
      this.rekapitulasi.technical[0].value =
        (this.rekapitulasi.technical[0].max / 100) *
        this.aspectHandler("pl", this.rating.pl);
      this.rekapitulasi.technical[1].value =
        (this.rekapitulasi.technical[1].max / 100) *
        this.aspectHandler("mk", this.rating.mk);
      this.rekapitulasi.technical[2].value =
        (this.rekapitulasi.technical[2].max / 100) *
        this.aspectHandler("ku", this.rating.ku);
      this.rekapitulasi.technical[3].value =
        (this.rekapitulasi.technical[3].max / 100) *
        this.aspectHandler("kl", this.rating.kl);
      this.rekapitulasi.technical[4].value =
        (this.rekapitulasi.technical[4].max / 100) *
        this.aspectHandler("at", this.rating.at);
      this.rekapitulasi.technical[5].value =
        (this.rekapitulasi.technical[5].max / 100) *
        this.aspectHandler("dp", this.rating.dp);
      this.rekapitulasi.technical[6].value =
        (this.rekapitulasi.technical[6].max / 100) *
        this.aspectHandler("dk", this.rating.dk);
      this.rekapitulasi.technical[7].value =
        (this.rekapitulasi.technical[7].max / 100) *
        this.aspectHandler("em", this.rating.em);
      this.rekapitulasi.technical[8].value =
        (this.rekapitulasi.technical[8].max / 100) *
        this.aspectHandler("ai", this.rating.ai);
      this.rekapitulasi.technical[9].value =
        (this.rekapitulasi.technical[9].max / 100) *
        this.aspectHandler("hs", this.rating.hs);
      this.rekapitulasi.technical[10].value =
        (this.rekapitulasi.technical[10].max / 100) *
        this.aspectHandler("ml", this.rating.ml);
      this.rekapitulasi.technical[11].value =
        (this.rekapitulasi.technical[11].max / 100) *
        this.aspectHandler("lb", this.rating.lb);
      this.rekapitulasi.technical[12].value =
        (this.rekapitulasi.technical[12].max / 100) *
        this.aspectHandler("sb", this.rating.sb);
    },
    passDATA2Store(data) {
      let payload = {};
      payload.id = data.GP_ID;
      payload.timestamp = data.CREATEDAT / 1000;
      payload.unitName = data.UK_NAME;
      payload.periode = data.GP_PERIODE;
      payload.kp = data.KP;
      payload.pg = data.PG;
      payload.pl = data.PL;
      payload.mk = data.MK;
      payload.ku = data.KU;
      payload.kl = data.KL;
      payload.at = data.AT;
      payload.dp = data.DP;
      payload.dk = data.DK;
      payload.em = data.EM;
      payload.ai = data.AI;
      payload.hs = data.HS;
      payload.ml = data.ML;
      payload.lb = data.LB;
      payload.sb = data.SB;
      this.$store.commit("setGPData", payload);
    },
    unixTS2DMY(timestamp) {
      return moment.unix(timestamp).format("DD/MM/YYYY");
    },
    levelHandler(rating) {
      if (rating >= 91) return "Excellent";
      else if (rating >= 81) return "Good";
      else if (rating >= 66) return "Medium";
      else if (rating >= 51) return "Poor";
      else return "Very Poor";
    },
    ratingHandler(data) {
      let aspect = {
        kp: 0,
        pg: 0,
        pl: 0,
        mk: 0,
        ku: 0,
        kl: 0,
        at: 0,
        dp: 0,
        dk: 0,
        em: 0,
        ai: 0,
        hs: 0,
        ml: 0,
        lb: 0,
        sb: 0,
      };
      Object.keys(aspect).forEach((key) => {
        aspect[key] = this.aspectHandler(key, data[key]);
      });

      return this.recaptHandler(aspect);
    },
    aspectHandler(aspect, data) {
      let value = 0;
      for (let i = 0; i < this.portion[aspect].length; i++) {
        value = this.percentageHandler2(aspect, data);
      }
      return value;
    },
    percentageHandler2(aspect, data) {
      let percentage = [];

      for (let i = 0; i < this.span[aspect].length; i++) {
        let sum = 0;
        for (
          let j = this.start[aspect][i];
          j < this.start[aspect][i] + this.span[aspect][i];
          j++
        ) {
          sum += Math.abs(data[j]);
        }
        percentage.push(
          (sum / (this.span[aspect][i] * 2)) * this.portion[aspect][i] * 100
        );
      }

      let total = 0;
      for (let i = 0; i < percentage.length; i++) {
        total += percentage[i];
      }

      return total;
    },
    recaptHandler(aspect) {
      Object.keys(aspect).forEach((key) => {
        aspect[key] *= this.max[key] / 100;
      });

      let recapt = 0;
      Object.values(aspect).forEach((value) => {
        recapt += value;
      });
      return recapt;
    },
    onCancel() {
      this.$router.push("daftar");
    },
  },
  created() {
    if (this.$store.getters.getGPData != null) {
      this.getStoreRating();
      this.renders.loading = false;
    } else {
      this.$router.push("daftar");
    }
  },
  destroyed() {},
};
</script>

<style lang="scss">
.rekapitulasi-rating {
  .el-table {
    th.is-right {
      .cell {
        display: flex;
        flex-flow: row;
        justify-content: flex-end;
      }
    }
    th.is-center {
      .cell {
        display: flex;
        flex-flow: row;
        justify-content: center;
      }
    }
  }
  .el-select--mini {
    .el-input--mini {
      input {
        height: 28px;
      }
    }
  }
}
</style>